import React from 'react';
import { Link } from 'gatsby';
const languages = require('../data/languages');

export default class StringHandler {
    static urlMenuString(item, classes) {
        let default_value = <a href="" className={classes}>{item.title}</a>;
        /*if(item.alias) {
            return <Link to={item.alias} className={classes}>{item.title}</Link>;
        } else if(item.link && item.link.url) {
            switch(item.link.link_type) {
                case 'Web':
                    default_value = <a href={item.link.url} className={classes}>{item.title}</a>;
                break;
                default:
                    default_value = <a href="" className={classes}>{item.title}</a>;
                break;
            }
        }*/
        return default_value;
    }
    static remoteLangueToLocal(lang) {
        let lang_mapped = this.inverseObject(languages.mappings);
        return lang_mapped[lang];
    }
    static inverseObject(object) {
        return Object.assign({}, ...Object.entries(object).map(([a,b]) => ({ [b]: a })));
    }
    static redirectI18n(context, lang) {
        return '/'+lang;
    }
    static pageContextFormater(pageContext){
        let context =  {lang: this.remoteLangueToLocal(pageContext.lang), slug : pageContext.slug};
        return context;
    }
}